  border-bottom: 1px solid rgba(0, 0, 0, 0.12);
}

@media (max-width: 600px) {
  .v-card-title {
    font-size: 1rem;
  }

  .subtitle-1 {
    font-size: 0.9rem;
  }
}
</style>
